  .no-text a#global-wish {
    background-image: url("/v2/image/wap/right_icon/icons_sprite/icons_sprite@2x_29025c38a6.png")
  }
}

#right-icon {
  height: 50px;
  width: 50px;
  border-radius: 26px;
  border: 1px solid #e1e1e1;
  position: fixed;
  bottom: 52px;
  right: 6px;
  z-index: 30;
  overflow: hidden;
  -webkit-transition: width ease-in-out .3s;
  -moz-transition: width ease-in-out .3s;
  transition: width ease-in-out .3s;
  background-color: white;
  background-clip: padding-box
}

#right-icon .right-icon-container {
  position: absolute;
  height: 50px;
  right: 0px;
  top: 0px
}

#right-icon .icon {
  float: left;
  width: 50px;
  height: 50px
}

#right-icon .show-more-btn {
  background-image: url("/v2/image/wap/right_icon/icons_sprite/icons_sprite_29025c38a6.png");
  background-repeat: no-repeat;
  background-size: 300px 150px;
  background-position: 1px -50px
}

#right-icon .show-more-btn.new {
  background-position: -99px -50px
}

#right-icon .icon-img {
  height: 32px
}

#right-icon .icon-txt {
  line-height: 18px;
  font-size: 12px;
  text-align: center;
  color: #9f9f9f
}

#right-icon.show {
  padding-left: 12px
}

#right-icon.show .show-more-btn {
  background-position: -49px -50px
}

#right-icon.no-text {
  border: 0px none;
  background-color: transparent
}

#right-icon.no-text .icon-txt,
#right-icon.no-text .icon-img {
  display: none
}

@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
only screen and (min--moz-device-pixel-ratio: 1.5),
only screen and (min-device-pixel-ratio: 1.5) {
  #right-icon .show-more-btn {
    background-image: url("/v2/image/wap/right_icon/icons_sprite/icons_sprite@2x_29025c38a6.png")
  }
}



